import Vue from 'vue'
import VueRouter from 'vue-router'
import SignIn from '../views/SignIn.vue'
import Wedding from '../views/Wedding.vue'
import Outdoor from '../views/Outdoor.vue'
import Weddingdress from '../views/Weddingdress.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/wedding',
    name: 'wedding',
    component: Wedding

  },

  {
    path: '/outdoor',
    name: 'outdoor',
    component: Outdoor
  },
  {
    path: '/weddingdress',
    name: 'weddingdress',
    component: Weddingdress
  },

  {
    path: '*',
    name: 'SignIn',
    component: SignIn
  },
]

const router = new VueRouter({
  mode: 'history',
  routes

})

export default router
